<template>
  <div id="app">
    <img class="logo" src="./assets/logo.png">
    <music :url = "" :silde = "true" :control = "control" :musicwave = "musicwave" :musiclist = "musiclist" :options = "options"></music>
    {{parseInt($children[0].musicfun.currentTime)}}/{{parseInt($children[0].musicfun.duration)}}
    <div class="test-box">
    <div class="input"><label>帽头高度: </label><input v-model = "capheight" placeholder="capheight"></input></div>

    <div class="input"><label>柱条间距: </label><input v-model = "gaprate" placeholder="gaprate"></input></div>
    
    <div class="input"><label>柱条颜色: </label><input v-model = "linecolor" placeholder="linecolor"></input></div>
   
    <div class="input"><label>帽头颜色: </label><input v-model = "capcolor" placeholder="capcolor"></input></div>
    
    <button class="test-btn" @click = "setcolor">改变颜色</button>
    
    <div class="input"><label>播放进度: </label><input v-model = "start" placeholder="start"></input></div>
    
    <button class="test-btn" @click = "setstart">改变进度</button>
    </div>
  </div>
</template>

<script>
import music from './music/music'

export default {
    components: {
        music
    },
    ready: function(){
        var self = this
        this.$children[0].musicfun.end = function() {
            self.$children[0].musicfun.next()
        }
        this.$children[0].musicfun.loadmusic(0)
    },
    data() {
        return {
            control: true,
            musicwave: {
                width: 320,
                height: 200
            },
            options: {
                config: {
                    linecolor : '#359F60',
                    gaprate: 0.5,
                    speed: 1,
                    capheight: 3,
                    capcolor: '#359F60'
                },
                musiclist: [
                    {url:'./static/music/のぶなが - 深海少女.mp3', name:'のぶなが - 深海少女.mp3'},
                    {url:'./static/music/朴惠京 - 爱情和友情之间.mp3', name:'朴惠京 - 爱情和友情之间.mp3'},
                    {url:'./static/music/花澤香菜 - Qunka!.mp3', name:'花澤香菜 - Qunka!.mp3'},
                    {url:'./static/music/Jamillions - One Night.mp3', name:'Jamillions - One Night.mp3'},
                    {url:'./static/music/李治廷 - Echoes of the Rainbow.mp3', name:'李治廷 - Echoes of the Rainbow.mp3'},
                    {url:'./static/music/岸部眞明 - 流れ行く云.mp3', name:'岸部眞明 - 流れ行く云.mp3'},
                    {url:'./static/music/雨宮天 - 月灯り - TV版.mp3', name:'雨宮天 - 月灯り - TV版.mp3'},
                    {url:'./static/music/川嶋あい - 時雨.mp3', name:'川嶋あい - 時雨.mp3'}
                ],
                size: 64
            },
            linecolor: null,
            capcolor: null,
            capheight: null,
            speed: null,
            start: null,
            gaprate: null,
            currenttime: null,
            alltime: null
        }
      },
    watch: {
        capheight : function (a,b) {
            if (!a) return
            this.$children[0].musicfun.config.capheight = parseFloat(a)
        },
        gaprate : function (a,b) {
            if (!a) return
            this.$children[0].musicfun.config.gaprate = parseFloat(a)
        }
    },
    methods: {
        setcolor : function () {
            if (this.linecolor) this.$children[0].musicfun.linecolor(this.linecolor)
            if (this.capcolor) this.$children[0].musicfun.capcolor(this.capcolor)
        },
        setstart : function () {
            this.$children[0].musicfun.audio.currentTime = this.start
        }
    }
}
</script>

<style>
input,button,select,textarea{outline:none;}
html {
  height: 100%;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

#app {
  color: #2c3e50;
  width: 320px;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  text-align: center;
}

#app a {
  color: #42b983;
  text-decoration: none;
}

.logo {
  width: 100px;
  height: 100px
}
.test-box {
    width: 100%;
    background-color: #eee;
}
.test-btn {
    width: 70%;
    height: 28px;
    background-color: #05AE87;
    border-radius: 15px;
    border-width: 0;;
    cursor: pointer;
    margin-bottom: 2px;
}
.input{
    width: 100%;
    height: 35px;
    position: relative;
}
.input label{
  font-size: 0.9rem;
  font-family: Source Sans Pro, Helvetica, sans-serif;
  min-height: 100%;
  height: 35px;
  line-height: 35px;
  display: block;
  position: absolute;
  top: 0px;
  left: 12%;
}
.input input{
    position: absolute;
    top: 0px;
    right: 12%;
    border-radius: 15px;
    text-align: center;
    transition: width 0.5s;
    -moz-transition: width 0.5s;  /* Firefox 4 */
    -webkit-transition: width 0.5s; /* Safari 和 Chrome */
    -o-transition: width 0.5s;
    width: 50%;
    height: 80%;
    /*display: block;*/
    margin: 0 auto;
    border: 1px solid #ccc;
    opacity: 0.7;
}
.input input:focus{
  width: 55%;
  transition: width 0.5s;
  -moz-transition: width 0.5s;  /* Firefox 4 */
  -webkit-transition: width 0.5s; /* Safari 和 Chrome */
  -o-transition: width 0.5s;
  -webkit-tap-highlight-color: transparent;
  tap-highlight-color: transparent;
  border:none;
  outline:medium;
  opacity: 1;
}
</style>



